import { InjectionToken, Provider } from '@angular/core';
import { Additions, BB_CONTACT_IBAN_COUNTRIES_LIST, IBANCountryList } from '@backbase/contact-common-ang';

const emptyIbanCountriesList: IBANCountryList = {};

export const BB_CONTACT_FORM_ADDITIONS_TOKEN: InjectionToken<ContactFormAdditions> = new InjectionToken(
  'BB_CONTACT_FORM_ADDITIONS_TOKEN',
);

export interface ContactFormAdditions {
  additions: Additions;
}

export const ContactConfigProviders: Array<Provider> = [
  {
    provide: BB_CONTACT_FORM_ADDITIONS_TOKEN,
    useValue: { additions: undefined },
  },
  {
    provide: BB_CONTACT_IBAN_COUNTRIES_LIST,
    useValue: emptyIbanCountriesList,
  },
];
